import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatAutocompleteTrigger } from '@angular/material/autocomplete';

import icSearch from '@iconify/icons-ic/twotone-search';

import { Observable, of } from 'rxjs';
import { debounceTime, map, mergeMap, startWith, tap } from 'rxjs/operators';

import {
  BodyParamNameResource, BodyParamResource, HeaderNameResource,
  HeaderResource, QueryParamNameResource, QueryParamResource
} from '@core/http';

@Component({
  selector: 'requests-search',
  templateUrl: './requests-search.component.html',
  styleUrls: ['./requests-search.component.scss'],

  // Enable this to add custom styling to MatInput
  // See: https://stackoverflow.com/questions/53684763/how-to-remove-space-bottom-mat-form-field
  encapsulation: ViewEncapsulation.None,
})
export class RequestsSearchComponent implements OnInit {
  @Input() formControl: FormControl;
  @Input() projectId: number;
  @Input() query: string;
  @Input() page: number;
  @Input() pageSize: number;

  @Output() search = new EventEmitter<string>();

  // Form settings
  basicSearch: boolean;
  currentOptions: Observable<string[]>;
  currentQuery: string;
  form: FormGroup;
  searchConstants = {
    fields: {
      BODY_PARAM: 'bodyParam',
      HEADER: 'header',
      HOST: 'host',
      METHOD: 'method',
      PATH: 'path',
      QUERY_PARAM: 'queryParam',
      STATUS: 'status',
    },
    ops: {
      AND: 'AND',
      OR: 'OR',
    },
    delimitters: {
      COLON: ':',
      PERIOD: '.',
      SPACE: ' ',
    },
    resources: {
      NAME: 'name',
      VALUE: 'value',
    },
  };

  // Icons
  icSearch = icSearch;

  constructor(
    private bodyParamResource: BodyParamResource,
    private bodyParamNameResource: BodyParamNameResource,
    private fb: FormBuilder,
    private headerResource: HeaderResource,
    private headerNameResource: HeaderNameResource,
    private queryParamResource: QueryParamResource,
    private queryParamNameResource: QueryParamNameResource
  ) { }

  ngOnInit(): void {
    this.basicSearch = true;

    this.formControl = this.formControl || (new FormControl(''));
    this.formControl.patchValue(this.query);
    this.form = this.fb.group({
      search: this.formControl,
    });

    const filteredSet: Observable<string[]> = this.formControl.valueChanges
      .pipe(
        debounceTime(500),
        startWith(''),
        tap(value => this.currentQuery = value),
        mergeMap(value => this.nextSearchOptions(value))
      );

    this.currentOptions = filteredSet;
  }


// API Access

  getNames(api): Observable<any> {
    return api.index({
      project_id: this.projectId,
    }).pipe(
      map((res: Array<any>) => {
        return res.map(component => component.name);
      })
    );
  }

  getValues(api): Observable<any> {
    return api.index({
      project_id: this.projectId,
    }).pipe(
      map((res: Array<any>) => {
        return res.map(component => component.value);
      })
    );
  }

// View Access

 /**
  *
  * When an option is selected, pad the option with an extra space at the end
  *
  */
  handleOptionSelect(event, value, trigger: MatAutocompleteTrigger) {
    event.stopPropagation();

    const curValue = this.currentQuery || '';

    switch (value) {
      case this.searchConstants.fields.BODY_PARAM:
      case this.searchConstants.fields.HEADER:
      case this.searchConstants.fields.QUERY_PARAM:
        let newValue = '';
        if (curValue.length) {
          newValue = `${curValue} ${value}.`;
        } else {
          newValue = `${value}.`;
        }

        this.formControl.patchValue(newValue);
        trigger.openPanel();
        break;
      case this.searchConstants.fields.HOST:
      case this.searchConstants.fields.METHOD:
      case this.searchConstants.fields.PATH:
      case this.searchConstants.fields.STATUS:
        this.formControl.patchValue(`${curValue}${value}:`);
        break;
      default:
        switch (this.lastDelimitter()) {
          case this.searchConstants.delimitters.PERIOD:
            this.formControl.patchValue(`${curValue}${value}:`);
            break;
          case this.searchConstants.delimitters.COLON:
            this.formControl.patchValue(`${curValue}${JSON.stringify(value)} `);
            break;
          default:
            this.formControl.patchValue(`${curValue} ${value}`);
        }

        trigger.openPanel();
    }
  }

 /**
  *
  * Emit search event
  *
  */
  submit() {
    this.search.emit(this.form.value.search);
  }

  toggleSearch() {
    this.basicSearch = !this.basicSearch;
    this.formControl.patchValue('');
  }

// Helpers

  private nextSearchOptions(value: string): Observable<string[]> {
    let resourceType = '';
    let resource;

    const lastTok = this.lastTok();
    console.debug(`Last Tok: ${lastTok}`);

    switch (lastTok) {
      case '':
      case this.searchConstants.ops.AND:
      case this.searchConstants.ops.OR:
        return of(Object.values(this.searchConstants.fields));
      default:
        const lastDelimitter = this.lastDelimitter();
        console.debug(`Last Delimitter: ${lastDelimitter}`);

        switch (lastDelimitter) {
          case this.searchConstants.delimitters.PERIOD:
            resourceType = this.searchConstants.resources.NAME;
            break;
          case this.searchConstants.delimitters.COLON:
            resourceType = this.searchConstants.resources.VALUE;
            break;
          case this.searchConstants.delimitters.SPACE:
            return of(Object.values(this.searchConstants.ops));
          default:
            return of(Object.values(this.searchConstants.fields));
        }
    }

    const lastComponent = this.lastField();
    console.debug(`Last Delimitter: ${lastComponent}`);

    switch (lastComponent) {
      case this.searchConstants.fields.HEADER:
        if (resourceType === this.searchConstants.resources.NAME) {
          return this.getNames(this.headerNameResource);
        } else {
          return this.getValues(this.headerResource);
        }
        break;
      case this.searchConstants.fields.QUERY_PARAM:
        if (resourceType === this.searchConstants.resources.NAME) {
          return this.getNames(this.queryParamNameResource);
        } else {
          return this.getValues(this.queryParamResource);
        }
        break;
      case this.searchConstants.fields.BODY_PARAM:
        if (resourceType === this.searchConstants.resources.NAME) {
          return this.getNames(this.bodyParamNameResource);
        } else {
          return this.getValues(this.bodyParamResource);
        }
        break;
    }
  }

  private lastDelimitter(): string {
    const delimitters = Object.values(this.searchConstants.delimitters);
    const characters = [...this.currentQuery].reverse();
    for (let i = 0; i < characters.length; ++ i) {
      const char = characters[i];

      if (delimitters.indexOf(char) !== -1) {
        if (i === 0) {
          return char;
        } else {
          // Check to the control character is not escaped
          if (characters[i - 1] !== '\\') {
            return char;
          }
        }
      }
    }

    return '';
  }

  private lastField(): string {
    const lastPhrase = this.lastPhrase();
    if (!lastPhrase.length) { return ''; }

    return lastPhrase.split('.')[0] || '';
  }

  private lastTok() {
    const query = this.currentQuery.trim();
    if (!query.length) { return ''; }

    const phrases = query.split(/\s+/);
    return phrases[phrases.length - 1];
  }

  private lastPhrase() {
    const query = this.currentQuery.trim();
    if (!query.length) { return ''; }

    const phrases = query.split(/\s+/);

    let lastPhrase = phrases[phrases.length - 1];
    switch (lastPhrase.toUpperCase()) {
      case this.searchConstants.ops.OR:
      case this.searchConstants.ops.AND:
        lastPhrase = phrases[phrases.length - 2] || '';
    }

    return lastPhrase;
  }
}
