import { Injectable } from '@angular/core';

import { FileDownloadRepresentation, FileDownloadValue } from '@data/interfaces';

import JSZip from 'jszip';

@Injectable({
  providedIn: 'root',
})
export class FileDownload {

  create(rep: FileDownloadRepresentation) {
    const numFiles = Object.keys(rep).length;
    if (numFiles == 0) { return; }

    if (numFiles == 1) {
      this.createSingle(rep);
    } else {
      this.createZip(rep);
    }
  }

  createSingle(rep: FileDownloadRepresentation) {
    const a = document.createElement('a');

    for (const key of Object.keys(rep)) {
      const value: string = rep[key];
      // const content = btoa(value);
      const content = btoa(unescape(encodeURIComponent(value)));

      a.href = `data:application/text;base64,${content}`;
      a.download = key;
      a.click();
      a.remove();
    }
  }

  createZip(rep: FileDownloadRepresentation) {
    const a = document.createElement('a');
    const zip = new JSZip();

    Object.entries(rep).forEach(([key, value]) => {
      zip.file(key, value);
    });

    zip.generateAsync({ type: 'base64' }).then(content => {
      a.href = `data:application/zip;base64,${content}`;
      a.download = 'test.zip';
      a.click();
      a.remove();
    });
  }
}
