import { SelectionModel } from '@angular/cdk/collections';
import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, TemplateRef, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';

import icCloudDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icDeleteForever from '@iconify/icons-ic/twotone-delete-forever';
import icEdit from '@iconify/icons-ic/twotone-edit';
import icFilterList from '@iconify/icons-ic/twotone-filter-list';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icSearch from '@iconify/icons-ic/twotone-search';
import icStar from '@iconify/icons-ic/twotone-star';
import icStarBorder from '@iconify/icons-ic/twotone-star-border';

import { fadeInUp400ms, scaleFadeIn400ms, stagger20ms } from '@vex/animations';
import { TableColumn } from '@vex/interfaces/table-column.interface';

@Component({
  selector: 'data-table',
  templateUrl: './data-table.component.html',
  styleUrls: ['./data-table.component.scss'],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
  animations: [
    stagger20ms,
    fadeInUp400ms,
    scaleFadeIn400ms,
  ],
})
export class DataTableComponent<T> implements OnInit, OnChanges, AfterViewInit {
  @Input() set data(value: T[]) {
    this.dataSource.data = value;
  }
  @Input() buttonsTemplate: TemplateRef<any>;
  @Input() columns: TableColumn<T>[];
  @Input() page = 0;
  @Input() pageSize = 20;
  @Input() pageSizeOptions = [10, 20, 50];
  @Input() length = 20;
  @Input() query = '';
  @Input() sortBy: string;
  @Input() sortOrder: string;
  @Input() searchTemplate: TemplateRef<any>;
  @Input() resourceName: string;
  @Input() templates: Array<TemplateRef<any>>;
  @Input() editable: boolean;

  @Output() toggleStar = new EventEmitter<number>();
  @Output() edit = new EventEmitter<number>();
  @Output() delete = new EventEmitter<number>();
  @Output() view = new EventEmitter<number>();
  @Output() paginate = new EventEmitter<any>();
  @Output() sort = new EventEmitter<any>();
  @Output() search = new EventEmitter<string>();
  @Output() globalEdit = new EventEmitter<any>();

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  // @ViewChild(MatSort, { static: true }) sort: MatSort;

  dataSource = new MatTableDataSource<T>();
  searchForm: FormGroup;
  selection = new SelectionModel<T>(true, []);

  icMoreVert = icMoreVert;
  icStar = icStar;
  icStarBorder = icStarBorder;
  icDeleteForever = icDeleteForever;
  icEdit = icEdit;
  icFilterList = icFilterList;
  icDelete = icDelete;
  icSearch = icSearch;

  constructor(
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    if (!this.searchTemplate) {
      const queryFormControl = new FormControl('');
      queryFormControl.patchValue(this.query);

      this.searchForm = this.fb.group({
        query: queryFormControl,
      });
    }
  }

  ngOnChanges(changes: SimpleChanges): void { }

  ngAfterViewInit() {
    // Uncommenting this line will set total rows to current rows provided rather than this.length
    // this.dataSource.paginator = this.paginator;
    // this.dataSource.sort = this.sort;
  }

  get visibleColumns(): Array<keyof T | string> {
    return this.columns.filter(column => column.visible).map(column => column.property);
  }

// Emit Event

  emitSearch(event) {
    const query = this.searchForm.value.query;
    this.search.emit(query);
  }

  emitToggleStar(event: Event, id: number) {
    event.stopPropagation();
    this.toggleStar.emit(id);
  }

  emitSort(event) {
    this.sort.emit(event);
  }

  removeSelected(requests: Array<any>) {
    requests.forEach(r => this.delete.emit(r.id));

    // Requests to be destroyed are no longer deemed selected
    this.selection.clear();
  }

  emitGlobalEdit(event) {
    this.globalEdit.emit(event);
  }

// View Access

 /**
  *
  * Selects all rows if they are not all selected; otherwise clear selection.
  *
  */
  masterToggle($event) {
    $event.preventDefault();

    if (this.isAllSelected()) {
      this.selection.clear();
    } else {
      if (this.isPartiallySelected()) {
        // If current page has something selected, then clear current page
        // Else select everything in current page

        let hasSelected = false;
        this.dataSource.data.some(row => {
          if (this.isSelected(row)) {
            hasSelected = true;
            return true;
          }
        });

        if (hasSelected) {
          this.selection.clear();
        } else {
          this.selectAll();
        }
      } else {
        this.selectAll();
      }
    }
  }

  toggleColumnVisibility(event, column) {
    column.visible = !column.visible;
  }

 /**
  *
  * previousPageIndex: 0
  * pageIndex: 0
  * pageSize: 50
  * length: 3
  *
  */
  onPaginateChange($event) {
    // this.requestsTableConfig.updatePageSize($event.pageSize);
  }

// Helpers

 /**
  *
  * Whether the number of selected elements matches the total number of rows.
  *
  */
  isAllSelected() {
    if (!this.selection.hasValue()) {
      return false;
    }

    const numSelected = this.selection.selected.length;
    const numRows = this.length;

    return numSelected === numRows;
  }

  isPartiallySelected() {
    return this.selection.hasValue() && !this.isAllSelected();
  }

  isSelected(row: T) {
    return this.selection.isSelected(row);
  }

  selectAll() {
    this.dataSource.data.forEach(row => this.selection.select(row));
  }
}
