import { Injectable } from '@angular/core';

import { AsyncSubject } from 'rxjs';

import { HttpService } from '@core/http/http.service';
import { FileService } from '@core/utils/file.service';
import { UriService } from '@core/utils/uri.service';

declare var require: any;

@Injectable({
  providedIn: 'root',
})
export class AliasDiscovery {
  private URI;

  constructor(
    private httpService: HttpService,
    private file: FileService,
    private uri: UriService
  ) {
    this.URI = require('url-parse');
  }

  getPathSegments(uri) {
    return uri.pathname.split('/').filter(segment => {
      return segment.length > 0;
    });
  }

  discover(url: string) {
    const subject = new AsyncSubject();
    const pathAliases$ = subject.asObservable();
    const pathAliases = [];

    let uri = new this.uri.class(url);
    let segments: string[] = this.getPathSegments(uri);

    for (let i = segments.length - 1; i >= 0; --i) {
      uri = new this.URI(url);
      segments = this.getPathSegments(uri);

      const segment = segments[i];

      segments[i] = '_ALIAS_';

      uri.pathname = this.file.join('/', segments.join('/'));
      uri.query = '';

      this.httpService.get(uri.toString()).subscribe(
        res => {
          pathAliases.push(true);
          subject.next(pathAliases);
          if (i === 0) { subject.complete(); }
        },
        error => {
          pathAliases.push(error.status && error.status !== 404);
          subject.next(pathAliases);
          if (i === 0) { subject.complete(); }
        }
      );
    } // for

    return pathAliases$;
  }

  discoverAll(urls: string[]) {
    const pathAliasesMap = {};

    const subject = new AsyncSubject();
    const pathAliasesMap$ = subject.asObservable();

    for (let i = 0; i < urls.length; ++i) {
      const url = urls[i];

      this.discover(url).subscribe(data => {
        pathAliasesMap[url] = data;
        subject.next(pathAliasesMap);

        if (i === urls.length - 1) {
            subject.complete();
        }
      });
    }

    return pathAliasesMap$;
  }
}
